.shadow{
  box-shadow: 0px 20px 30px 0px rgba(0, 0, 0, 0.05);
}
.transition{
  transition: .3s ease;
}

.overlay {
  &:before {
    content: '';
    background: rgba(125, 113, 211, 0.9);
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
  }
}

